.image {
    width: 250px;
    height: 100px;
    border-radius: 8px;
}

.card {
    box-shadow: 0 8px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 250px;
    margin: 4rem;
    border-radius: 8px;
    height: 350px;
}

.card:hover {
    opacity: 60%;
    box-shadow: 0 12px 24px 0 rgba(165,55,253,1);
    border-radius: 8px;
}

.container {
    padding: 2px 16px;
}

.a {
    font-style: italic;
    font-weight: lighter;
    font-size: smaller;
}

.box {
    display: flex;
    justify-content: space-between;
    
    flex-wrap: wrap;
    margin-left: 20%;
}